.sessionTrackerContainer {
  padding: 10px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.sessionTrackerTitle {
  font-size: 24px;
  margin-bottom: 20px;
}

.sessionTrackerError {
  color: red;
  margin-bottom: 20px;
}

.sessionTrackerForm {
  margin-bottom: 30px;
}

.sessionTrackerInput, .sessionTrackerTextarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

.sessionTracker-teacher-leader-combobox {
  width: 100%; /* Full width */
  padding: 0.5em; /* Spacing */
  border: 1px solid #ccc; /* Border color */
  border-radius: 4px; /* Rounded corners */
  transition: border-color 0.3s; /* Smooth transition */
}

.sessionTracker-teacher-leader-combobox:focus {
  border-color: #007BFF; /* Change border color on focus */
  outline: none; /* Remove default outline */
}


.sessionTrackerTextarea {
  height: 100px;
}

.sessionTrackerMembersFieldset {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
}

.sessionTrackerMemberLabel {
  display: block;
  margin-bottom: 5px;
}

.sessionTrackerCheckbox {
  margin-right: 10px;
}

.sessionTrackerButton {
  padding: 8px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-right: 10px;
}

.sessionTrackerButton:hover {
  background-color: #0056b3;
}

.sessionTrackerDeleteButton {
  background-color: #dc3545;
}

.sessionTrackerDeleteButton:hover {
  background-color: #c82333;
}

.sessionTrackerList {
  margin-top: 20px;
}

.sessionTrackerListTitle {
  font-size: 20px;
  margin-bottom: 10px;
}

.sessionTrackerTable {
  width: 100%;
  border-collapse: collapse;
}

.sessionTrackerTable th, .sessionTrackerTable td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.sessionTrackerTable th {
  background-color: #f4f4f4;
}

.sessionTrackerTable tr:nth-child(even) {
  background-color: #f9f9f9;
}

.bacenta-label {
  display: flex;
  flex-direction: column;
  font-family: 'Arial', sans-serif;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  margin-bottom: 1.5rem;
}

.bacenta-selector {
  width: 100%;
  padding: 10px;
  margin-top: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 1rem;
  background-color: #f9f9f9;
  transition: border-color 0.3s ease;
}

.bacenta-selector:focus {
  border-color: #007bff;
  outline: none;
}

.bacenta-selector-item {
  color: #333;
  padding: 10px;
}

@media (min-width: 768px) {
  .bacenta-selector {
    width: 50%;
  }
}
